$accent: #161616;
$main: #F53B04;
$danger: #F32013;
$bg: #fff;
$headFont: 'Miriam Libre', sans-serif;
$body: 'Roboto', sans-serif;

@import url('https://fonts.googleapis.com/css2?family=Miriam+Libre:wght@400;700&family=Roboto&display=swap');

@mixin flex($j:center, $a:flex-start, $d: row, $wrap: nowrap) {
  display: flex;
  align-items: $a;
  justify-content: $j;
  flex-flow: $d $wrap; }

html, body, #root, .App {
  width: 100vw;
  height: 100vh;
  margin: 0;
  * {
    transition: all 200ms ease-in-out; } }

.App {
  @include flex($j: flex-start, $a: center, $d: column); }

div,button, input, a, p, section, img {
  &:focus {
    outline: 2px dashed $main;
    box-shadow: 0 0 10px $main; } }

body {
  background: $bg;
  overflow-x: hidden;
  margin: 0;
  color: lighten($accent, 30%);
  font-family: $body; }

a {
  text-decoration: none; }

p {
  display: inline; }

body {
  &:focus {
    outline: 1px solid $accent; } }

header {
  @include flex($j: space-between, $a: center, $wrap: wrap);
  width: 80vw;
  padding: 0 10px;
  box-shadow: 0 1px 2px rgba(0,0,0,0.05),0 1px 4px rgba(0,0,0,0.05),0 2px 8px rgba(0,0,0,0.05);
  nav {
    @include flex(); } }



.link {
  color: $accent;
  background: transparent;
  @include flex($a: center);
  height: 2rem;
  width: 4rem;
  font-size: 1rem;
  margin: 5px;
  border: none;
  outline: none;
  cursor: pointer;
  &:hover {
    background: $main;
    color: $bg; } }

.link.active {
  background: $main;
  color: $bg; }

.items-container {
  height: 100%;
  @include flex(center, center, $wrap: wrap); }


.item {
  @include flex(center, center);
  width: 250px;
  margin: 20px;
  box-sizing: border-box;
  padding: 10px;
  flex-direction: column;
  box-shadow: 0 0 10px darken($bg, 20%);
  .image {
    width: 200px;
    height: 200px;
    overflow: hidden;
    margin: auto;
    @include flex(center, center); }

  > * {
    margin: 5px; } }

.details {
  width: 200px;
  @include flex(center, flex-start);
  span {
    font-size: 0.8em;
    padding-left: 10px; }

  flex-direction: column;
  box-sizing: border-box;
  * {
    margin: 4px 0; } }

.item {
  img {
    height: 200px; } }


/*
  -----------------
    Button
  -----------------
  */

.btn {
  padding: 15px;
  cursor: pointer;
  outline: none;
  border: none;
  background: $main;
  color: $bg;
  font-family: $body;
  @include flex($a:center);
  height: 2.2em;
  border-radius: 5%;
  &:hover {
    background: darken($main, 5%); } }


.slim {
  padding: 0;
  font-size: 0.8rem;
  width: 2rem;
  height: 2.5rem;
  background: transparent;
  color: $main;
  border: 1px solid $main;
  height: auto;
  &:hover {
    background: $main;
    color: $bg; } }

.subtle {
  padding: 10px;
  background: transparent;
  color: lighten($accent, 50%);
  &:hover,&:focus {
    color: $danger;
    background: transparent;
    outline: none;
    box-shadow: none; } }






/*
  -----------------
    CartBox
  -----------------
  */


.cartBox {
  position: absolute;
  background: $bg;
  top: 95px;
  right: 9.4vw;
  z-index: 999;
  padding: 10px;
  padding-bottom: 20px;
  overflow-y: auto;
  min-width: 300px;
  min-height: 40px;
  max-height: 80vh;
  box-shadow: 0 0 10px darken($bg, 20%);
  @include flex($j: flex-start, $a: center,$d: column);
  .empty {
    @include flex($a:center);
    margin-top: auto;
    margin-bottom: auto; }
  .cartItem {
    @include flex($j: space-between, $a: center);
    margin: 5px;
    .image {
      width: 100px;
      height: 100px;
      img {
        width: 100%; } }
    * {
      margin: 5px; } } }

.hidden {
  display: none !important; }




.incrementBox {
  @include flex($j:space-evenly,$a:center); }

.close {
  position: absolute;
  top: 5px;
  right: 5px; }



/*
  -----------------
    Orders
  -----------------
  */

#orders {
  justify-content: flex-start; }

.ordersPage {
  @include flex($j: space-evenly, $a: center);
  width: 80%;
  margin: 10px auto;
  .checkoutBox {
    min-width: 150px;
    padding: 40px;
    border: 1px solid lighten($accent, 50%); } }


.ordersContainer {
  flex-basis: 60%;
  @include flex($a:flex-start, $d:column); }


.orderItem {
    @include flex($j:space-evenly ,$a:center);
    margin: 10px auto;
    flex-basis: 100%;
    .image {
      flex-basis: 100px;
      img {
        max-width: 100%; } }
    .details {
      span {
        font-size: 0.7em; }
      text-align: center;
      @include flex($j: space-evenly, $a: center);
      color: lighten($accent, 20%);
      flex: 0.7;
      > * {
        flex-basis: 100px; } } }



/*
  -----------------
    Content
  -----------------
  */


section {
  @include flex($a:center, $d: column);
  width: 98%;
  margin:auto {}
  flex-grow: 1;
  margin: 10px;
  text-align: center; }

h1,h2,h3,h4,h5,h6 {
  color: $accent;
  font-family: $headFont; }

.name {
  color: $accent;
  font-size: 1.2em; }


#home {
  height: calc(100vh - 90px); }

#home, #about {
  justify-content: flex-start; }


/*
  ------------------
    Check Out
  ------------------
  */

.checkOut {
  position: absolute;
  @include flex($a: center, $d:column);
  background: $bg;
  width: 500px;
  height: 250px;
  box-shadow: 0 0 5px darken($bg, 20%);
  top: calc(50vh - 125px);
  left: calc(50vw - 250px); }


/*
  --------------------
  --------------------
    Mobile Styles
  --------------------
  --------------------
  */

// CartBox

@media screen and ( max-width: 700px ) {
  .cartBox {
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    @include flex($j: flex-start, $a: center,$d: column);
    .cartItem {
      width: 95%;
      justify-content: space-between; } }
  .ordersPage {
    flex-wrap: wrap;
    .ordersContainer {
      flex-basis: 100%;
      margin: 20px auto;
      .orderItem {
        .details {
          flex-wrap: wrap; } } } } }
